import * as SC from './UserMenu.styled';
import { Box } from 'components/Box';
import { useLogoutMutation } from 'redux/authApi';
import { logoutUser } from 'redux/authSlice';
import { useDispatch } from 'react-redux';

export const UserMenu = ({ user, isOpen, closeModal, refUserMenu }) => {
  const initials = name => name[0].toUpperCase();
  const [logout] = useLogoutMutation();
  const dispatch = useDispatch();

  const handleLogoutUser = async e => {
    try {
      closeModal();
      await logout().unwrap();
      dispatch(logoutUser());
    } catch (error) {}
  };

  return (
    <SC.UserMenuBox isOpen={isOpen}>
      <SC.UserMenu isOpen={isOpen} ref={refUserMenu} id="userMenu">
        <Box display="flex" flexDirection="column" alignItems="center">
          <SC.AvatarBox>
            <p>{initials(user.name)}</p>
          </SC.AvatarBox>
          <SC.UserName>{user.name}</SC.UserName>
        </Box>
        <div>
          <SC.LogoutBtn type="button" onClick={handleLogoutUser}>
            Logout
          </SC.LogoutBtn>
        </div>
      </SC.UserMenu>
    </SC.UserMenuBox>
  );
};
